body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header-color {
  background: #bdc3c7; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to bottom,
    #2c3e50,
    #bdc3c7
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.landing-grid {
  /* background: #bdc3c7;  fallback for old browsers */
  /* background: -webkit-linear-gradient(to bottom, #2c3e50, #bdc3c7);  /* Chrome 10-25, Safari 5.1-6 */
  /* background: linear-gradient(to bottom, #2c3e50, #bdc3c7); W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  background-image: url("https://images.iphonephotographyschool.com/22682/How-To-Blur-Background-On-iPhone.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: fixed;
  size: auto;
  width: 100%;
  height: 100%;
  padding: 0 !important;
}

.avatar-img {
  height: 250px;
  padding-top: 5en;
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: auto;
  border-radius: 20%;
}

.banner-text {
  background-color: black;
  opacity: 0.8;
  width: 75%;
  margin: auto;
  border-radius: 10px;
}

.banner-text h1 {
  font-weight: bold;
  color: white;
  text-align: center;
}

.banner-text p {
  color: white;
  font-size: 20px;
  padding: 1em;
  text-align: center;
}

.banner-text hr {
  border-top: 5px dotted white;
  width: 50%;
  margin: auto;
}

.social {
  display: flex;
  justify-content: center;
}

.social i {
  color: white;
  font-size: 3em;
  padding: 8px;
}

.projects-grid {
  display: flex;
  width: 100%;
  flex-wrap: wrap;
}
.projects-grid1 {
  display: flex;
  width: 100%;
  flex-wrap: wrap;
}

.contact-body {
  background-image: url("https://images.iphonephotographyschool.com/22682/How-To-Blur-Background-On-iPhone.jpg");
  background-position: center;
  background-repeat: no-repeat;
  margin: auto;
  position: static;
  position: initial;
  width: 100%;
  height: 100%;
  justify-content: center;
  padding-top: 2em;
}

.contact-grid {
  text-align: center;
  justify-content: center;
  width: 80%;
  height: 600px;
  background: rgba(255, 255, 255, 0.562);
  color: black;
  margin-bottom: 20px;
}

.contact-grid h2 {
  font-family: "Anton", sans-serif;
}

.contact-grid img {
  height: 250px;
}

.contact-grid p {
  font-family: "Oxygen", sans-serif;
  font-weight: bold;
  width: 75%;
  margin: auto;
  padding-top: 1em;
}

.contact-grid hr {
  border-top: 5px dotted black;
  width: 96%;
  margin: auto;
}

.contact-list li{
  width:100%;
  height:20px;
  text-align: center;
  font-size: 30px;
  font-family: Anton;
}
